import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getAreas, deleteArea } from 'services/area';

class ListAreaPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteArea(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'M',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#zones/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Khu vc">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#zones/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getAreas} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListAreaPage />
  </Page>
);



